.hamburger {
  z-index: 9999;
  width: 2.5rem;
  padding-block: 2.1875rem;
  padding-inline: 1.875rem;
  height: inherit;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}

.hamburger span {
  position: absolute;
  left: 0.5625rem;
  display: inline-block;
  width: 2.5rem;
  height: 2px;
  transition: transform 0.5s;
  background-color: white;
}

.hamburger.is-open {
  right: 1.25rem;
}

.hamburger span:nth-child(1) {
  top: 1.8125rem;
}

.hamburger span:nth-child(2) {
  top: 2.5rem;
  width: 1.875rem;
  transition: opacity 0.5s;
}

.hamburger span:nth-child(3) {
  top: 3.1875rem;
  width: 1.25rem;
}

.hamburger.hamburger.is-open span:nth-child(1) {
  transform: translateY(0.5625rem) rotate(-135deg);
  background-color: white;
}

.hamburger.hamburger.is-open span:nth-child(2) {
  opacity: 0;
}

.hamburger.hamburger.is-open span:nth-child(3) {
  transform: translateY(-0.8125rem) rotate(135deg);
  background-color: white;
  width: 2.5rem;
}

